import {
  code,
  Random
} from "./mockConfig";

/**
 * @method postData
 * @param
 * @returns 状态码、文章列表的数据
 * @desc 随机生成文章数据
 */
const postData = () => {
  let dataList = []; // 用于存放文章数据的数组
  for (let i = 0; i < 10; i++) {
    let post = {
      title: Random.ctitle(10, 25), // 随机生成长度为10-25的标题
      icon: Random.dataImage("250x250", "文章icon"), // 随机生成大小为250x250的图片链接
      author: Random.cname(), // 随机生成名字
      date: Random.date() + " " + Random.time() // 随机生成年月日 + 时间
    };
    dataList.push(post);
  };
  // 返回状态码和文章数据posts
  return {
    code,
    dataList
  };
};

/**
* @method sellerData
* @param
* @returns 商家数据列表
* @desc 商家信息
*/
