      "name": Random.ctitle(10, 25),
      "description": Random.cparagraph(1, 1),
      "deliveryTime": Random.date("mm"),
      "score": Random.float(0, 5, 0, 1),
      "serviceScore": Random.float(0, 5, 0, 1),
      "foodScore": Random.float(0, 5, 0, 1),
      "rankRate": Random.float(0, 100, 0, 1),
      "minPrice": Random.integer(0, 100),
      "deliveryPrice": Random.integer(0, 20),
